.wrapper {
    /*position: relative;*/
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
    height: 50px;
}

.sidebarContainer {
    position: fixed;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 40px;
    transition: 0.5s;
    transform: translateX(-100%);
}

.sidebarContainerActive {
    transform: translateX(0%);
}

.link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
}

.link a {
    display: block;
    font-weight: bold;
    text-decoration: none;
    color: darkslategrey;
    margin-bottom: 4px;
}

.link:hover {
    background-color: #ccc;
    border-radius: 4px;
}

.link:active {
    background-color: darkgrey;
}

.sidebarBtn {
    position: absolute;
    top: 10px;
    right: -20px;
    width: 20px;
    height: 20px;
    background-color: darkslategrey;
}

.sidebarBtn:active {
    background-color: gray;
}

